/*_Delete_vars_*/ 
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0 !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

a {
  color: #3488dd;
  text-decoration: none;
  background-color: transparent;
}
a:hover {
  color: #1c62a8;
  text-decoration: underline;
}

a:not([href]):not([class]) {
  color: inherit;
  text-decoration: none;
}
a:not([href]):not([class]):hover {
  color: inherit;
  text-decoration: none;
}

pre,
code,
kbd,
samp {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg {
  overflow: hidden;
  vertical-align: middle;
}

table {
  border-collapse: collapse;
}

caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #6c757d;
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
  text-align: -webkit-match-parent;
}

label {
  display: inline-block;
  margin-bottom: 0.5rem;
}

button {
  border-radius: 0;
}

button:focus:not(:focus-visible) {
  outline: 0;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

[role=button] {
  cursor: pointer;
}

select {
  word-wrap: normal;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button:not(:disabled),
[type=button]:not(:disabled),
[type=reset]:not(:disabled),
[type=submit]:not(:disabled) {
  cursor: pointer;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type=radio],
input[type=checkbox] {
  box-sizing: border-box;
  padding: 0;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
  cursor: pointer;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

.fade {
  transition: opacity 0.15s linear;
}
@media (prefers-reduced-motion: reduce) {
  .fade {
    transition: none;
  }
}
.fade:not(.show) {
  opacity: 0;
}

.collapse:not(.show) {
  display: none;
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height 0.35s ease;
}
@media (prefers-reduced-motion: reduce) {
  .collapsing {
    transition: none;
  }
}
.collapsing.width {
  width: 0;
  height: auto;
  transition: width 0.35s ease;
}
@media (prefers-reduced-motion: reduce) {
  .collapsing.width {
    transition: none;
  }
}

/* icons */
.icon {
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  display: inline-block;
}

button {
  display: inline-flex;
  justify-content: center;
  font-size: 18px;
  line-height: 20px;
  font-weight: 600;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: 0.3s;
}
button:focus-visible {
  outline: none;
}
@media (max-width: 1400px) {
  button {
    font-size: 16px;
  }
}

.btn-green,
.btn-blue,
.btn-yellow,
.btn-circle,
.btn-login-google,
.btn-login-fb,
.btn-login,
.btn-g-pay {
  display: inline-flex;
  justify-content: center;
  font-size: 18px;
  line-height: 20px;
  font-weight: 600;
  padding: 20px 30px;
  border-radius: 50px;
  transition: 0.3s;
}
@media (max-width: 1400px) {
  .btn-green,
  .btn-blue,
  .btn-yellow,
  .btn-circle,
  .btn-login-google,
  .btn-login-fb,
  .btn-login,
  .btn-g-pay {
    padding: 20px;
    font-size: 16px;
  }
}
@media (max-width: 767.98px) {
  .btn-green,
  .btn-blue,
  .btn-yellow,
  .btn-circle,
  .btn-login-google,
  .btn-login-fb,
  .btn-login,
  .btn-g-pay {
    padding: 15px;
  }
}

.nav-collapse {
  padding-top: 30px;
  margin-right: 30px;
  white-space: nowrap;
}
@media (max-width: 767.98px) {
  .nav-collapse {
    padding-top: 15px;
    margin-right: 15px;
  }
}
.nav-collapse:last-child {
  margin-right: 0;
}
.nav-collapse:last-child .btn-drop {
  padding-right: 0;
}
.nav-collapse .btn-drop {
  display: flex;
  align-items: center;
  padding: 0;
  margin-bottom: 15px;
  font-weight: 600;
  line-height: 16px;
  color: #323232;
  font-family: "Nunito Sans", sans-serif;
}
@media (max-width: 767.98px) {
  .nav-collapse .btn-drop {
    margin-bottom: 5px;
    font-size: 14px;
  }
}
.nav-collapse .btn-drop .drop-icon {
  min-width: 12px;
  transform: rotateX(180deg);
  margin-left: 8px;
  transition: 0.3s;
}
.nav-collapse .btn-drop.collapsed .drop-icon {
  transform: rotateX(0);
}
.nav-collapse .header-nav-menu .menu-item,
.nav-collapse .footer-nav-menu .menu-item {
  font-size: 0;
  transition: 0.1s;
}
.nav-collapse .header-nav-menu .menu-item-link,
.nav-collapse .footer-nav-menu .menu-item-link {
  font-size: inherit;
}
.nav-collapse .collapse.show .header-nav-menu .menu-item,
.nav-collapse .collapse.show .footer-nav-menu .menu-item {
  font-size: 14px;
}
@media (max-width: 767.98px) {
  .nav-collapse .collapse.show .header-nav-menu .menu-item,
  .nav-collapse .collapse.show .footer-nav-menu .menu-item {
    font-size: 12px;
  }
}

