.btn-circle {
  position: relative;
  display: inline-flex;
  text-align: left;
  color: #323232;
  font-weight: 600;
  transition: 0.5s;
}
@media (max-width: 767.98px) {
  .btn-circle {
    padding: 15px 25px;
  }
}
.btn-circle .text {
  position: relative;
  font-size: inherit;
  font-weight: inherit;
  z-index: 1;
}
@media (max-width: 767.98px) {
  .btn-circle .text {
    font-size: 14px;
  }
}
.btn-circle::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 60px;
  height: 60px;
  border-radius: 50px;
  background-color: #FAD101;
  z-index: 0;
  transition: inherit;
}
@media (max-width: 767.98px) {
  .btn-circle::before {
    width: 50px;
    height: 50px;
  }
}
.btn-circle:hover {
  color: #323232;
}
@media (min-width: 992px) {
  .btn-circle:hover::before {
    width: 100%;
  }
}
@media (max-width: 1199.98px) {
  .btn-circle:focus::before {
    width: 100%;
  }
}
.btn-circle:active::before {
  background-color: #FFE874;
}

